import {buildFeatureInfoUrl} from './wms';

// Datos simulados para las estadsticas de los sectores rurales de Repeln
const mockData = {
	'Sector Villa Rosa': {
		'rea Total': '2,500 hectreas',
		'Uso Principal': 'Agrcola',
		Distribucin: {
			'Uso Agrcola': 65,
			'Uso Residencial': 20,
			'Uso Ganadero': 15,
		},
	},
	'Sector Rotinet': {
		'rea Total': '1,800 hectreas',
		'Uso Principal': 'Mixto',
		Distribucin: {
			'Uso Agrcola': 40,
			'Uso Residencial': 35,
			'Uso Ganadero': 25,
		},
	},
	'Sector Las Tablas': {
		'rea Total': '2,200 hectreas',
		'Uso Principal': 'Ganadero',
		Distribucin: {
			'Uso Agrcola': 30,
			'Uso Residencial': 25,
			'Uso Ganadero': 45,
		},
	},
	'Sector Cienaguita': {
		'rea Total': '1,600 hectreas',
		'Uso Principal': 'Agrcola',
		Distribucin: {
			'Uso Agrcola': 55,
			'Uso Residencial': 30,
			'Uso Ganadero': 15,
		},
	},
};

export function updateStatistics(map, sectorRuralLayer, zonaHomogeneaLayer) {
	const statsContent = document.getElementById('stats-content');
	if (!statsContent) {
		console.error('No se encontr el elemento stats-content');
		return;
	}

	let htmlContent = '';

	// Verificar si las capas estn activas
	const sectorRuralActive = map.hasLayer(sectorRuralLayer);
	const zonaHomogeneaActive = map.hasLayer(zonaHomogeneaLayer);

	if (!sectorRuralActive && !zonaHomogeneaActive) {
		htmlContent =
			'<div class="no-data-message">Activa las capas de Sector Rural o Zona Homognea para ver las estadsticas.</div>';
	} else {
		Object.entries(mockData).forEach(([sector, datos]) => {
			htmlContent += `
				<div class="sector-stats">
					<div><b>${sector}</b></div>
					<div class="stat-item">
						<span class="stat-label">rea Total:</span>
						<span class="stat-value">${datos['rea Total']}</span>
					</div>
					<div class="stat-item">
						<span class="stat-label">Uso Principal:</span>
						<span class="stat-value">${datos['Uso Principal']}</span>
					</div>
					<div class="stat-distribution">
						<div class="distribution-title">Distribucin de Uso:</div>
						${Object.entries(datos.Distribucin)
							.map(
								([uso, porcentaje]) => `
								<div class="stat-item">
									<span class="stat-label">${uso}:</span>
									<span class="stat-value">${porcentaje}%</span>
									<div class="progress-bar">
										<div class="progress" style="width: ${porcentaje}%"></div>
									</div>
								</div>
							`
							)
							.join('')}
					</div>
				</div>
			`;
		});
	}

	statsContent.innerHTML = htmlContent;
}
